import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Acao_plano_acao_hp } from '../../../modelo/Acao_plano_acao_hp'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_plano_acao_hp-dados-details',   templateUrl: 'acao_plano_acao_hp_dadosDetails.component.html' }) export class Acao_plano_acao_hpDadosDetailsComponent implements OnInit {    acao_plano_acao_hp: Acao_plano_acao_hp;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.acao_plano_acao_hp.Acao_plano_acao_hp";                 let id = +params['id'];         this.acao_plano_acao_hp = new Acao_plano_acao_hp();         this.acao_plano_acao_hp.cd_acao_plano_acao_hp = id;         o.objetoJson = this.acao_plano_acao_hp;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a acao_plano_acao_hp. '; }      this.acao_plano_acao_hp = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


